
.dialogs { /*в диалогы заложено 2 дивки и поэтому является гридом*/
    display: grid;
    grid-template-columns: 2fr 10fr; /*наш грид состоит из двух коло (первая - 2 фр, а вторая - 10фр)*/
}
.dialogsItems{ /*вложенные*/
    padding: 10px; /*для удобства расстояние (от текста)*/
    color: white;
}
.messages {
    padding: 10px;
}
.messages .message{
    color:white;
}
.dialogsItems .active{
    color: gold;
}
.item {
    color: greenyellow;
}
.item img {
    width: 50px;
    height: 50px;
    border-radius: 30px;
}